import React, { Component } from "react";
import PropTypes from "prop-types";
import { Typography } from "@material-ui/core";
import * as util from "../SpotifyUtil";
import SpotifyPlayListSelector from "../SpotifyPlaylistSelector";
import {
  AUTH_EXPIRE_MS,
  CLIENT_ID,
  SPOTIFY_AUTH_SCOPES,
} from "../SpotifyConstants";

class SpotifyConnection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      selectedPlaylist: "",
      spotifyUser: null,
      avatar: null,
      playlists: [],
      invalidSpotifyToken:
        !localStorage.getItem("spotifytoken") ||
        Date.now() - localStorage.getItem("spotifytoken_timestamp") >
          AUTH_EXPIRE_MS,
    };
    this.createPlayer = this.createPlayer.bind(this);
    this.setPlaylist = this.setPlaylist.bind(this);
  }

  async componentDidMount() {
    const { game } = this.props;
    const { invalidSpotifyToken } = this.state;
    const token = localStorage.getItem("spotifytoken");

    // If there is no token, redirect to Spotify authorization. kolla om token gtt ut ocks
    // jag kan stta redirect till ngon anna route och lsa det s att nr man kommer dit s gr den direkt och kollar i firebase och connectar n till rtt game?

    //TODO Det fungerar inte med andra konton n mitt riktigt.. kanske fr att den kopplad till appen?
    //TODO ngot har ndrats kanske, tror vi anvnde refreshtokens m.m. nr vi gjorde PE hackathon grejen...
    //TODO se exempel i spotofy docs getting started med auth.. det tokenet jag fr hr fr kalle testsson r invalid.. det kanske r ett refreshtoken?
    //TODO kanske ska anvndasd vidare?

    //TODO men konstigaste r ju att det fungerar fr mitt riktiga konto. fr ett riktigt token d..

    if (invalidSpotifyToken) {
      const authEndpoint = "https://accounts.spotify.com/authorize";
      // Replace with your app's client ID, redirect URI and desired scopes
      const redirectUri = window.location.origin + window.location.pathname;
      localStorage.setItem("spotify_type", "play");

      localStorage.setItem("RecentGameIdPlay", game.gameId);
      window.location = `${authEndpoint}?client_id=${CLIENT_ID}&redirect_uri=${redirectUri}&scope=${SPOTIFY_AUTH_SCOPES.join(
        "%20"
      )}&response_type=token&show_dialog=true`;
    }
    const myHeaders = new Headers();
    myHeaders.append("Authorization", `Bearer ${token}`);
    const getHeader = {
      method: "GET",
      headers: myHeaders,
    };

    const profileResponse = await fetch(
      "https://api.spotify.com/v1/me",
      getHeader
    );
    const profileResult = await profileResponse.json();

    let hasNext = true;
    let offset = 0;
    const limit = 50;
    const userPlaylists = [];
    while (hasNext) {
      const playlistsResponse = await fetch(
        `https://api.spotify.com/v1/users/${
          profileResult.id
        }/playlists?limit=${limit}&offset=${offset}`,
        getHeader
      );
      const playlistsResult = await playlistsResponse.json();
      userPlaylists.push(...playlistsResult.items);
      if (playlistsResult.items.length < limit) {
        hasNext = false;
      } else {
        offset += limit;
      }
    }

    const existingPlaylists = Object.values(game.players || {}).map(
      (x) => x.playlist
    );
    const selectablePlaylists = userPlaylists.filter(
      (x) => !existingPlaylists.includes(x.id) && util.isValidPlaylist(x)
    );

    this.setState(() => ({
      playlists: selectablePlaylists,
      name: profileResult.display_name,
      avatar:
        profileResult.images.length > 0 ? profileResult.images[0].url : null,
    }));
  }

  setPlaylist(selectedPlaylist) {
    this.createPlayer(selectedPlaylist);
  }

  handleChange = (name) => (event) => {
    this.setState({
      [name]: event.target.value,
    });
  };

  createPlayer(selectedPlaylist) {
    const { name, avatar } = this.state;
    const { addPlayer } = this.props;
    // jag skulle kunna spara userid istllet fr name och avatar. och sen i host ta ut avatar och name
    const player = {
      name,
      playlist: selectedPlaylist,
      score: 0,
      avatar,
    };
    addPlayer(player);
    this.setState({ selectedPlaylist });
  }

  render() {
    const { game, playerKey } = this.props;
    const { playlists, selectedPlaylist, invalidSpotifyToken } = this.state;
    if (invalidSpotifyToken) {
      return <span>Loading...</span>;
    }
    let playerName = "";
    if (game.players && playerKey && game.players[playerKey]) {
      playerName = game.players[playerKey].name;
    }
    // fiska fram info om vilken playlist som blev vald hr? anvnd playlists och selectedplaylist (id) fr att gra det
    return (
      <div className="phase-container">
        {selectedPlaylist ? (
          <div>
            <div className="quiz-top-section" />
            <div className="quiz-middle-section">
              <Typography variant="h5">
                <span>Vlkommen </span>
                <span className="dynamic-text">{playerName}</span>
              </Typography>
              <Typography variant="subtitle1" style={{ marginTop: 15 }}>
                {
                  "Ditt namn syns nu p spelskrmen. Nr spelet startar kommer frgor att presenteras och du svarar hr p din enhet. Du kan vlja ett eller flera svarsalternativ. Du fr ett pong fr varje rtt svar och ett minus fr varje felaktigt svar. Du kan inte f minuspong p en frga."
                }
              </Typography>
            </div>
            <div className="quiz-bottom-section" />
          </div>
        ) : (
          <div>
            <SpotifyPlayListSelector
              setSelection={this.setPlaylist}
              playlists={playlists}
            />
          </div>
        )}
      </div>
    );
  }
}
SpotifyConnection.propTypes = {
  game: PropTypes.object.isRequired,
  playerKey: PropTypes.string,
  addPlayer: PropTypes.func.isRequired,
};
export default SpotifyConnection;
